<script setup>  
import {ref, onMounted} from 'vue';
const words=ref([
["Saturday","شنبه"],
["Sunday","یکشنبه"],
["Monday","دوشنبه"],
["Tuesday","سه شنبه"],
["Wednesday","چهارش"],
["Thursday","پنج"],
["Friday","جمعه"],
["date","تاریخ"],
["month","ماه"],
["year","سال"],
["January","ژانویه"],
["February","فوریه"],
["March ","مارس"],
["April","آوریل"],
["May","می"],
["June","ژوئن"],
["July ","جولای","ژوئیه"],
["August","آگوست یا اوت"],
["September","سپتامبر"],
["October","اکتبر"],
["November","نوامبر"],
["December","دسامبر"],
["one","یک"],
["two","دو"],
["three","سه"],
["four","چهار"],
["five","پنج"],
["six","شش"],
["seven","هفت"],
["eight","هشت"],
["nine","نُه"],
["ten","ده"],
["eleven","یازده"],
["twelve","دوازده"],
["thirteen","سیزده"],
["fourteen","چهارده"],
["fifteen","پانزده"],
["sixteen","شانزده"],
["seventeen","هفده"],
["eighteen","هجده"],
["nineteen","نوزده"],
["twenty","بیست"],
["thirty","سی"],
["forty","چهل"],
["fifty","پنجاه"],
["sixty","شصت"],
["seventy","هفتاد"],
["eighty","هشتاد"],
["ninety","نود"],
["one hundred","صد"],
  ]);


function gogo(event) {
    let card = event.target.parentNode;
    if (event.target.nodeName=="SPAN") {
          card = event.target.parentNode.parentNode;
    }

    card.classList.toggle("mori-flip");

    if (event.target.parentNode.classList.contains("front")|| event.target.classList.contains("front")) {
      document.getElementById(event.target.textContent).play();
    }
};

onMounted(()=>{
          for (let i of words.value) {
                var audioTag=document.createElement('audio');
                audioTag.src="../../assets/audio/"+i[0]+".mp3";
                audioTag.id=i[0];
                document.body.appendChild(audioTag);
          }
});
</script>



<template>
<div class="grid grid-cols-2 w-[100vw] justify-center items-center bg-blue-600/50 mt-4 font-normal">

<div v-for="item in words" >
    <div class="cont w-[45vw] h-[45vw] block mb-10" >                    
        <div class="card w-[100%] h-[100%] ml-[2vw] mr-[1vw]"  @click.capture="gogo($event)"> 
                    <div class="front text-xl flex "><span class="m-auto" v-text="item[0]"></span></div>
                    <div class="back text-xl flex"><span class="m-auto" v-text="item[1]"></span></div>
        </div>
    </div>
</div> 

</div>
</template>








<style>  

.cont{
  background-color: transparent;
  perspective: 1000px;
}

.card{
  width: 100%;
  height: 100%;
  transition: transform .9s;
  transform-style: preserve-3d;
}

.back , .front{     /*front and back class*/  
  box-shadow: 0px 0px 3px 2px gray, inset 0 0 10px 0px blue;
  background-image: url('../../img/cards_back.jpg'); 
  background-position: center center;
  background-size: 100%;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility:hidden;
  position: fixed;
  left: 0;
}

.front{
  background-color: blue;
}
.back{
  background-color: blue;
  transform: rotateY(180deg);
}


.mori-flip{       /*class for onclick toggle class with js or jquery*/
  transform: rotateY(180deg);
}

.card-gif img{
  width: 100%;
  height: 100%;
 }

</style>
