<script setup>  
import {ref, onMounted} from 'vue';
const words=ref([
['school','مدرسه'],
['classroom','کلاس درس'],
['door','درب'],
['blackboard','تخته سیاه'],
['desk','میز تحریر'],
['chair','صندلی'],
['pen','خودکار'],
['pencil','مداد'],
['book','کتاب'],
['notebook','دفتر-دفترچه'],
['ruler','خط کش'],
['dictionary','لغتنامه'],
['eraser','پاک کن'],
]);


function gogo(event) {
    let card = event.target.parentNode;
    if (event.target.nodeName=="SPAN") {
          card = event.target.parentNode.parentNode;
    }

    card.classList.toggle("mori-flip");

    if (event.target.parentNode.classList.contains("front")|| event.target.classList.contains("back")) {
      let ij =event.target.textContent;
      document.getElementById(ij).play();
      console.log(ij);
    }
};

onMounted(()=>{
          for (let i of words.value) {
                var audioTag=document.createElement('audio');
                audioTag.src="../../src/assets/audio/"+i[0]+".mp3";
                console.log(audioTag.src);
                audioTag.id=i[0];
                document.body.appendChild(audioTag);
          }
});
</script>



<template>
  <!-- <audio src="http://localhost:8080/src/assets/audio/school.mp3" id="school" autobuffer></audio> -->
<div class="grid grid-cols-2 w-[100vw] justify-center items-center bg-blue-600/50 mt-4 font-normal">

<div v-for="item in words" >
    <div class="cont w-[45vw] h-[45vw] block mb-10" >                    
        <div class="card w-[100%] h-[100%] ml-[2vw] mr-[1vw]"  @click.capture="gogo($event)"> 
                    <div class="front text-xl flex "><span class="m-auto" v-text="item[0]"></span></div>
                    <div class="back text-xl flex"><span class="m-auto" v-text="item[1]"></span></div>
        </div>
    </div>
</div> 

</div>
</template>








<style>  

.cont{
  background-color: transparent;
  perspective: 1000px;
}

.card{
  width: 100%;
  height: 100%;
  transition: transform .9s;
  transform-style: preserve-3d;
}

.back , .front{     /*front and back class*/  
  box-shadow: 0px 0px 3px 2px gray, inset 0 0 10px 0px blue;
  background-image: url('../../img/cards_back.jpg'); 
  background-position: center center;
  background-size: 100%;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility:hidden;
  position: fixed;
  left: 0;
}

.front{
  background-color: blue;
}
.back{
  background-color: blue;
  transform: rotateY(180deg);
}


.mori-flip{       /*class for onclick toggle class with js or jquery*/
  transform: rotateY(180deg);
}

.card-gif img{
  width: 100%;
  height: 100%;
 }

</style>
